<template>
  <div class="container">
    <div id="chart">
      <h1 style="color: #Fff">Скринер акции</h1>
      <p style="color: #fff">Изменение в процентах относительно цены закрытия предыдущей торговой сессии</p>
      <apexchart type="treemap" height="600" :options="chartOptions" :series="series" v-if="isReady"></apexchart>
    </div>
  </div>
</template>
<script>
export default {
  data() {
    return {
      isReady: false,
      series: [
        {
          data: [],
        },
      ],
      chartOptions: {
        legend: {
          show: false,
        },
        dataLabels: {
          enabled: true,
          style: {
            fontSize: "12px",
          },
        },
        plotOptions: {
          treemap: {
            enableShades: true,
            shadeIntensity: 0.1,
            reverseNegativeShade: true,
            colorScale: {
              ranges: [
                {
                  from: -7,
                  to: 0,
                  color: "#f63538",
                },
                {
                  from: 0.001,
                  to: 7,
                  color: "#30cc5a",
                },
              ],
            },
          },
        },
      },
    };
  },
  async mounted() {
    await fetch("http://otan-invest.gdh.kz/api/getScreenerData").then(async (res) => {
      let response = JSON.parse(await res.json());
      console.log(response.result.q);
      for (const item of response.result.q) {
        let change = (((item.ltp - item.pp) / item.pp) * 100).toFixed(2);
        console.log(item.pcp);
        this.series[0].data.push({ x: item.name, y: item.pcp });
      }
	  this.isReady = true;
    });
  },
};
</script>
<style scoped>
#chart {
}
</style>
