<template>
  <div class="home__chartDiv">
    <div class="d-flex align-items-end">
      <p class="mb-0 mr-2">{{info.short_name}}</p>
    </div>
    <p class="chartDiv__ticker">{{info.nt_ticker}}</p>
    <div id="chart">
      <apexchart type="area" height="100%" :options="chartOptions" :series="series" v-if="dataReady"/>
    </div>
  </div>
</template>
<script>
export default {
  props: ["data", "xSeries", 'info'],
  name: "stockCard",
  data() {
    return {
      dataReady: false,
      series: [
        {
          name: "Цена",
          data: [],
        },
      ],
      chartOptions: {
        colors: ["#29cc3f"],
        dataLabels: {
          enabled: false,
        },
        stroke: {
          curve: "smooth",
        },
        grid: {
          show: false,
        },
        legend: {
          show: false,
        },
        tooltip: {
          theme: "dark",
          x: {
            show: false,
          },
        },
        xaxis: {
          categories: [],
          lines: {
            show: false,
          },
          labels: {
            show: false,
          },
          axisBorder: {
            show: false,
          },
          axisTicks: {
            show: false,
          },
          crosshairs: {
            show: false,
          },
        },
        yaxis: {
          lines: {
            show: false,
          },
          labels: {
            show: false,
          },
          axisBorder: {
            show: false,
          },
          axisTicks: {
            show: false,
          },
          crosshairs: {
            show: false,
          },
        },
      },
    };
  },
  async mounted() {
    for (const candle of this.data) {
      this.series[0].data.push(candle[3]);
    }

    for (const time of this.xSeries) {
      let someDate = new Date(null);
      someDate.setSeconds(time);
      let candleDate = someDate.toLocaleString("ru").split(",");
      this.chartOptions.xaxis.categories.push(candleDate[0]);
    }

    setTimeout(() => {
      this.dataReady = true;
    }, 500)
  },
};
</script>
<style lang=""></style>
