import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'
import Screener from '../views/Screener.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'Home',
    component: Home
  },
  {
    path: '/screener',
    name: 'Screener',
    component: Screener
  },
]

const router = new VueRouter({
  // mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
