<template>
  <div id="app">
    <Loading v-if="sid == ''"></Loading>
    <Header :sidVal="sid" v-if="sid != ''" :isLogged="isLoggedIn"></Header>
    <router-view></router-view>
  </div>
</template>

<script>
import Header from '@/components/Header.vue'
import Loading from '@/components/Loading.vue'
import Home from '@/views/Home.vue'

export default {
  components: {Header,Loading,Home},
  data(){
    return {
      sid: '',
      isLoggedIn: false,
    }
  },
  methods: {
    async getSid(){
      let res = await fetch('http://otan-invest.gdh.kz/api/getSID')
      let data = await res.json();
      this.sid = data;
    }
  },
  mounted(){
    this.getSid();
  }
};
</script>

<style>
@import './assets/css/bootstrap.min.css';
@import './assets/css/style.css';  
</style>