<template>
  <header>
    <div class="container d-flex align-items-center justify-content-between header__main">
      <div class="container d-flex align-items-center">
        <a href=""><img src="../assets/img/logo.svg" alt="" class="header__logo" /></a>
        <nav class="d-flex align-items-center">
          <a href="/" class="nav__link">Главная</a>
          <!-- <a href="" class="nav__link">Криптовалюта</a> -->
          <a href="/screener" class="nav__link">Скринер</a>
        </nav>
        <!-- <div class="nav__searchBox">
          <input type="text" class="nav__search" placeholder="Поиск акции..." />
          <div class="d-flex nav__searchPopular">
            <p class="mr-2">Популярные</p>
            <strong class="mr-2">Газпром</strong>
            <strong class="mr-2">Халык Банк</strong>
            <strong class="mr-2">Каспи Банк</strong>
          </div>
        </div> -->
      </div>
      <div class="d-flex align-items-center" v-if="isLoggedIn">
        <p class="header__profileText">Привет, Азамат</p>
        <button class="header__profileBtn"></button>
      </div>
    </div>
    <div class="header__ticker d-flex align-items-center">
      <marquee v-if="marqueeAllData.length != 0" style="width: 100%">
        <span class="ticker__text" :class="{ marqueeItem_up: item.ISUP }" :key="item.NAME" v-for="item in marqueeData">
          <span class="marqueeItem_name">{{ item.NAME }}</span> <span class="marqueeItem_value">{{ item.VAL.toLocaleString("ru") }}</span>
          <span class="marqueeItem_name text-muted" :class="{ marqueeItem_red: !item.ISUP, marqueeItem_green: item.ISUP }"> ({{ item.PERC }}%)</span>
        </span>
      </marquee>
      <div class="d-flex" v-if="marqueeAllData.length == 0">
        <div class="marquee__skeleton"></div>
        <div class="marquee__skeleton"></div>
        <div class="marquee__skeleton"></div>
        <div class="marquee__skeleton"></div>
        <div class="marquee__skeleton"></div>
        <div class="marquee__skeleton"></div>
        <div class="marquee__skeleton"></div>
        <div class="marquee__skeleton"></div>
        <div class="marquee__skeleton"></div>
        <div class="marquee__skeleton"></div>
        <div class="marquee__skeleton"></div>
        <div class="marquee__skeleton"></div>
        <div class="marquee__skeleton"></div>
        <div class="marquee__skeleton"></div>
        <div class="marquee__skeleton"></div>
        <div class="marquee__skeleton"></div>
        <div class="marquee__skeleton"></div>
        <div class="marquee__skeleton"></div>
        <div class="marquee__skeleton"></div>
        <div class="marquee__skeleton"></div>
      </div>
      <div class="header__marqueeSelector">
        <strong class="marqueeSelector__btn" :class="{ marqueeSelector__btn_active: marqueeShow == 'indexes' }" @click="marqueeShow = 'crypto'">Криптовалюта</strong>
        <strong class="marqueeSelector__btn" :class="{ marqueeSelector__btn_active: marqueeShow == 'currencies' }" @click="marqueeShow = 'currency'">Валюты</strong>
        <strong class="marqueeSelector__btn" :class="{ marqueeSelector__btn_active: marqueeShow == 'all' }" @click="marqueeShow = 'all'">Все</strong>
      </div>
    </div>
  </header>
</template>

<script>
export default {
  props: ["sidVal", "isLogged"],
  data() {
    return {
      marqueeAllData: [],
      sid: this.sidVal,
      isLoggedIn: this.isLogged,
      tickers: ["KSPI.KZ", "KZAP.KZ"],
      marqueeShow: "all",
    };
  },
  computed: {
    marqueeData() {
      if (this.marqueeShow == "crypto") {
        return this.marqueeAllData.filter((item) => item.TYPE == "crypto");
      }
      if (this.marqueeShow == "currency") {
        return this.marqueeAllData.filter((item) => item.TYPE == "currency");
      }
      if (this.marqueeShow == "all") {
        return this.marqueeAllData;
      }
    },
  },
  methods: {
    async getData() {
      const WebSocketsURL = "wss://wss.tradernet.ru?SID=" + this.sid;

      const ws = new WebSocket(WebSocketsURL);

      var tickersToWatchChanges = this.tickers;

      ws.onmessage = function (m) {
        const [event, data] = JSON.parse(m.data);
        // console.log(data.ltp);
      };
      ws.onopen = function () {
        ws.send(JSON.stringify(["quotes", tickersToWatchChanges]));
      };
      // console.log(this.sid)

      let res = await fetch("http://otan-invest.gdh.kz/api/getMarqueeData");
      let data = await res.json();
      for (const item of data) {
        this.marqueeAllData.push(item);
      }
    },
  },
  mounted() {
    this.getData();
  },
};
</script>
