<template>
  <div class="container">
    <section class="home__chartsSection">
      <div class="home__chartsBlock">
        <stock-card class="chartCard" v-for="(item, index) in charts" :key="index" :xSeries="xSeries[index]" :data="item" :info="info[index]"/>
      </div>
    </section>
  </div>
</template>

<script>
import LargeCard from '../components/LargeCard.vue';
import StockCard from "../components/StockCard.vue";
export default {
  components: { StockCard, LargeCard },
  name: "Home",
  data() {
    return {
      charts: [],
      xSeries: [],
      info: []
    };
  },
  async mounted() {
    let dateNow = new Date();
    let yearNow = dateNow.getFullYear();
    let yearPast = dateNow.getFullYear() - 1;
    let month = dateNow.getMonth() + 1;
    if (month < 10) {
      month = "0" + month;
    }

    let from = "01" + "." + month + "." + yearPast + " " + "00:00";
    let to = "01" + "." + month + "." + yearNow + " " + "00:00";

    var params = {
      cmd: "getHloc",
      params: {
        id: "HSBK.KZ,KSPI.KZ,KZTO.KZ,KZTK.KZ,CCBN.KZ,KCEL.KZ",
        count: 0,
        timeframe: 10080,
        date_from: from,
        date_to: to,
        intervalMode: "ClosedRay",
      },
    };

    let vm = this;

    let dateToShow = new Date();
    dateToShow.setFullYear(dateNow.getFullYear() + 1);
    dateToShow.setMonth(dateNow.getMonth());
    dateToShow.setDate("01");

    let response = await $.getJSON("https://tradernet.ru/api/", { q: JSON.stringify(params) }, function (res) {
      vm.charts = res.hloc;
      vm.xSeries = res.xSeries;
      vm.info = res.info;
    });
  },
};
</script>

<style>
.chartCard:not(:last-child){
  margin-right: 20px;
  margin-bottom: 40px;
}
.chartsSection__editBtn {
  color: #408c43;
  margin-bottom: 10px;
  font-weight: 700;
  font-size: 0.75em;
  cursor: pointer;
  transition-duration: 0.2s;
}
.chartsSection__editBtn:hover {
  color: #fff;
}

.home__chartsBlock {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}

.home__chartDiv {
  color: #fff;
  width: 400px;
  height: 230px;
  padding: 20px;
  background-color: #161b22;
  border: 1px solid #1f2836;
  border-radius: 5px;
}

.chartDiv__price {
  font-size: 0.65em;
}
.chartDiv__ticker {
  font-size: 0.7em;
  color: rgb(207, 207, 207);
}
</style>
